.pokeListContainer{
    position: relative;
    background-color: rgb(26, 90, 90);
    display:flex;
    flex-wrap:wrap;
    min-height:350px;
}

.load-more-btn-container{
    align-self: flex-end;
    width:100%;
    margin: 20px 0;
    text-align: center;
}

.load-more-btn{
    color:#fff;
    background-color: rgb(81, 8, 177);
    cursor: pointer;
    padding:5px 10px;
}